<template>
  <section class="add-new-withdrawal">
    <div class="add-new-withdrawal__main">
      <div class="add-new-withdrawal__select-currency">
        <Input model-value="BTC" read-only class="add-new-withdrawal__select-currency__input">
          <template #prepend>
            <Icon name="btc-circle" />
          </template>
          <template #append>
            <Icon name="arrow-down" />
          </template>
        </Input>
        <Switcher v-model="isManual" class="add-new-withdrawal__select-currency__switcher">
          <template #first>
            <div class="switcher-option">
              <Icon name="lightning" />
              {{ t('common.quick') }}
            </div>
          </template>
          <template #second>
            <div class="switcher-option">
              <Icon name="settings" />
              {{ t('common.manual') }}
            </div>
          </template>
        </Switcher>
      </div>
      <div class="add-new-withdrawal__amount">
        <div class="add-new-withdrawal__label add-new-withdrawal__amount__label">{{ t('commands.enterAmount') }}</div>
        <div class="add-new-withdrawal__amount__fields">
          <Input
            :placeholder="t('amount.currency', { currency: 'BTC'})"
            hide-shadow
            full-opacity="0.5"
          >
            <template #append>
              <p>BTC</p>
            </template>
          </Input>
          <Icon name="arrows" />
          <Input
            :placeholder="t('amount.usd')"
            hide-shadow
            full-opacity="0.5"
          >
            <template #append>
              <p>USD</p>
            </template>
          </Input>
        </div>
      </div>
      <div class="add-new-withdrawal__address">
        <div class="add-new-withdrawal__label add-new-withdrawal__address__label">{{ t('places.address') }}</div>
        <Input
          v-model="withdrawal.address.value"
          :error="withdrawal.address.error"
          :error-text="withdrawal.address.errorText"
          placeholder="E.g. c15pJxcAIzaSyB9puDplMxJ62vYlFruxmmIAez3"
          :placeholder-styles="{
            opacity: 0.2
          }"
          hide-shadow
          @update:model-value="validate(withdrawal.address)"
        >
          <template #append>
            <Icon :name="withdrawal.address.error ? 'red-close' : 'green-checked'" />
          </template>
        </Input>
      </div>
      <div class="add-new-withdrawal__priority">
        <div>
          <div class="add-new-withdrawal__priority__label">{{ t('priority.title') }}</div>
          <RadioButtons
            v-model="priority"
            :data="radioButtons"
          >
            <template #default="{ item }">
              <span class="radio-buttons__text">{{ item.title }}</span>
            </template>
          </RadioButtons>
        </div>
        <div>
          <Text variant="p" type="muted" size="14px" mb="12px">{{ t('withdrawals.currentRate') }}</Text>
          <Text size="16px">$80,131.00</Text>
        </div>
        <div>
          <Text variant="p" type="muted" size="14px" mb="12px">{{ t('withdrawals.networkFee') }}:</Text>
          <Text size="16px">0.001231 BTC</Text>
          <Text size="16px" type="muted"> ~ $316.91</Text>
        </div>
      </div>
      <Container
        radius="8px"
        muted
        class="add-new-withdrawal__process"
      >
        <Button type="primary" @click="showAlert">{{ t('withdrawals.process') }}</Button>
        <Text variant="p" type="muted">
          <Text mr="20px">{{ t('withdrawals.balanceAfterTransaction') }}:</Text>
          100.01231 BTC ~ $56,180,131.00
        </Text>
      </Container>
      <Container
        radius="8px"
        muted
        class="add-new-withdrawal__authentication"
      >
        <Text weight="500" size="18px" mb="10px">{{ t('authentication.twoFactor') }}</Text>
        <Text size="14px" mb="20px">{{ t('authentication.subText', { app: '2FA' }) }}</Text>
        <InputCode class="add-new-withdrawal__authentication__code" />
        <div class="add-new-withdrawal__authentication__actions">
          <Button type="primary" @click="showAlert">{{ t('withdrawals.process') }}</Button>
          <Button class="add-new-withdrawal__authentication__actions-cancel">{{ t('common.cancel') }}</Button>
        </div>
      </Container>
    </div>
    <aside class="add-new-withdrawal__aside">
      <PreviousAddressesList />
    </aside>
  </section>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import Input from '@/components/UIKit/Input'
import Switcher from '@/components/UIKit/Switcher'
import { reactive, ref } from 'vue'
import RadioButtons from '@/components/UIKit/RadioButtons'
import { useI18n } from 'vue-i18n'
import Button from '@/components/UIKit/Button'
import { useStore } from 'vuex'
import Text from '@/components/Text/Text'
import Container from '@/components/Container'
import InputCode from '@/components/UIKit/InputCode'
import PreviousAddressesList from '@/views-admin/treasury/Withdrawals/PreviousAddressesList'

export default {
  name: 'AddNewWithdrawal',
  components: { PreviousAddressesList, InputCode, Container, Text, Button, RadioButtons, Switcher, Input, Icon },
  setup () {
    const { t } = useI18n()
    const store = useStore()
    const isManual = ref(false)
    const priority = ref(null)
    const withdrawal = reactive({
      address: {
        value: '',
        required: true,
        error: false,
        errorText: 'Detailed error message'
      }
    })
    const radioButtons = reactive([
      { title: t('priority.values.low'), key: 'low' },
      { title: t('priority.values.medium'), key: 'medium' },
      { title: t('priority.values.high'), key: 'high' }
    ])
    const showAlert = () => {
      store.dispatch('alert/showAlert', {
        id: new Date(),
        time: 3000,
        type: 'success',
        title: 'Successful withdrawal'
      })
    }
    const validate = (field) => {
      if (field.required) {
        field.error = !field.value
      }
    }
    return {
      t,
      priority,
      isManual,
      radioButtons,
      withdrawal,
      showAlert,
      validate
    }
  }
}
</script>

